function sliderAbout() {
    var swiper = new Swiper('.about__slider .mySwiper', {
        slidesPerView: 1,
        spaceBetween: 24,
        navigation: {
            nextEl: '.about__slider .swiper-button-next',
            prevEl: '.about__slider .swiper-button-prev',
        },
    })
}

function sliderAbout2() {
    var swiper = new Swiper('.about__slider2 .mySwiper', {
        slidesPerView: 5,
        spaceBetween: 30,
        navigation: {
            nextEl: '.about__slider2 .swiper-button-next',
            prevEl: '.about__slider2 .swiper-button-prev',
        },
        breakpoints: {
            320: {
                slidesPerView: 2,
                spaceBetween: 12,
            },
            577: {
                slidesPerView: 3,
                spaceBetween: 24,
            },
            769: {
                slidesPerView: 4,
                spaceBetween: 24,
            },
            1401: {
                slidesPerView: 5,
                spaceBetween: 30,
            }
        }
    })
}



$(document).ready(function() {

    sliderAbout();
    sliderAbout2();

    //Burger
    $('.burger').click(function () {
        $(this).toggleClass('burger--active')
        $('.header__list').toggleClass('header__list--active')
        $('body').toggleClass('fixed-body')
    })

    // Select
    $('.base-value').click(function () {
        $(this).parent('.select').toggleClass('select--active')
    })

    $('.select-values .value').click(function () {
        var currentele = $(this).html()
        $(this).parents('.select').find('.base-value').html(currentele)
        $(this).parents('.select').removeClass('select--active')
        $(this).siblings(".select-values .value").removeClass("value--active")
        $(this).addClass("value--active")
    })

    // Star
    $('.star').click(function () {
        $(this).addClass('active')
    })

    // Up
    var btn = $('.up');
    $(window).scroll(function() {
        if ($(window).scrollTop() > 300) {
            btn.addClass('up--active');
        } else {
            btn.removeClass('up--active');
        }
    });
    btn.on('click', function(e) {
        e.preventDefault();
        $('html, body').animate({scrollTop:0}, '300');
    });

})